import Vue from 'vue';
import App from './App.vue';
import Router from 'vue-router';

import Resource from 'vue-resource'
import MintUI from 'mint-ui';

Vue.use(Router);
Vue.use(Resource);
Vue.use(MintUI);
window.BUS = new Vue();
window.IP = 'http://192.168.0.16';
Vue.config.devtools = true;
new Vue({
  el: '#app',
  render: h => h(App)
})



